
import Loading from 'vux/src/components/loading/index.vue'

import InfiniteLoading from 'vue-infinite-loading'
import api from '@/utils/api'
import Filters from './pagelet/Filters'
import ActivityItem from './pagelet/ActivityItem'

export default {
name: 'ActivityList',
components: {
Loading,
InfiniteLoading,
Filters,
ActivityItem
},
data () {
return {
listData: [],
selectConditions: {},
loading: false,
page: 1,
currentConditions: {
typeId: undefined,
status: undefined,
other: undefined
}
}
},
methods: {
setData (data = []) {
this.listData.splice(0)
this.listData = data
},
getOptions () {
const { typeId, status, other } = this.currentConditions
const body = { page: this.page }
this.page++

if (typeId !== undefined) {
body.typeId = typeId
}
if (status !== undefined) {
body.status = status
}
if (other !== undefined) {
body.other = other
}
return body
},
async infiniteHandler ($state) {
try {
const body = this.getOptions()
const data = await api.getActivityList(body)
const len = data.length
if (len > 0) {
this.listData = this.listData.concat(data)
if (len < 10) {
$state.loaded()
} else {
$state.complete()
}
} else {
$state.complete()
}
} catch (err) {
console.error(err)
}
},
async getSelectConditions () {
this.selectConditions = await api.getSelectConditions()
},
handleSelect (type, value) {
if (type === 'type') {
if (value !== '') {
this.currentConditions.typeId = +value
} else {
delete this.currentConditions.typeId
}
}

if (type === 'status') {
if (value !== '') {
this.currentConditions.status = +value
} else {
delete this.currentConditions.status
}
}

if (type === 'other') {
if (value !== '') {
this.currentConditions.other = value
} else {
delete this.currentConditions.other
}
}
this.page = 1
this.fetchData()
},
async fetchData (type, value) {
this.loading = true
try {
const body = this.getOptions()
const data = await api.getActivityList(body)
this.setData(data)
} catch (err) {
console.error(err)
}
this.loading = false
}
},
mounted () {
this.getSelectConditions()
this.fetchData()
}
}



