
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'


export default {
name: 'Filters',
props: {
selectConditions: {
type: Object,
default: {}
}
},
data () {
return {
typeId: '',
status: '',
other: ''
}
},
components: {
Flexbox,
FlexboxItem
},
methods: {
onSelect ($event, type) {
this.$emit('select', type, $event.target.value)
}
}
}



