import pageConfig from '@/utils/pageConfig'
import { getTimeRange } from '@/utils/utils'

export default {
  name: 'ActivityItem',
  props: {
    activity: Object
  },
  data () {
    return {
      activityDetailLink: pageConfig.activityDetailLink
    }
  },
  computed: {
    getAppointTime () {
      const { starttime, endtime } = this.activity
      return getTimeRange(starttime, endtime)
    }
  },
  methods: {
    formatBadge (status) {
      switch (status) {
        // 0-未开始 1-预约中 2-已结束
        case 1:
          return this.activity['count-down']
          // return `离结束3天14个小时`
        case 2:
          return '已结束'
        default:
          return '未开始'
      }
    },
    formatStatus (status) {
      // 0-未开始 1-预约中 2-已结束
      switch (status) {
        case 1:
          return '预约'
        case 2:
          return '终了'
        default:
          return '未开始'
      }
    }
  }
}



